.container-image-text{
  .image-strate{
    width: 70%;
    margin: 0 auto;
    display: block;
  }

  .text-content{
    position: relative;
    top: 50%;
    transform: translateY(-50%);

    p{
      max-width: 100%;
    }
  }
}


@media (max-width: 768px) {
  .container-image-text{
    .image-strate{
      width: 100%;
      margin-bottom: 30px;
    }
  }
}